html {
    height: 100%;
}

body {
    height:100%;
    background-color: lightgrey;
}

.container {
    display:flex; 
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content-avatar {
    position: relative;
    bottom: -95px;
    border: lightgrey solid 20px;
    border-radius: 50%;
    background-color: white;
    z-index: 1;
  }

.content-box {
    padding-top: 90px;
    position: relative;
    text-align: center;
    background-color: white;
    font-family: 'Fira Sans';
    width:100%;
    height:100%;
}

